<template>
  <div class="main-container" :class="containerClasses">
    <the-header @open-sidebar="openSidebar"></the-header>
    <sidebar-block :contacts="contacts" :is-mobile-sidebar-open="isMobileSidebarOpen" @close-sidebar="closeSidebar"></sidebar-block>
    <base-container>
      <topic-list :topic-data="topics.education" #default="slotProps">
        <topic-education-item :topic-item-data="slotProps.topicItemData"></topic-education-item>
      </topic-list>

      <topic-list :topic-data="topics.experience" #default="slotProps">
        <topic-experience-item :topic-item-data="slotProps.topicItemData"></topic-experience-item>
      </topic-list>

      <topic-list :topic-data="topics.skills" #default="slotProps">
        <topic-skills-item :topic-item-data="slotProps.topicItemData"></topic-skills-item>
      </topic-list>

      <topic-list :topic-data="topics.projects" #default="slotProps">
        <topic-projects-item :topic-item-data="slotProps.topicItemData"></topic-projects-item>
      </topic-list>

      <topic-list :topic-data="topics.certificates" #default="slotProps">
        <topic-certificates-item :topic-item-data="slotProps.topicItemData"></topic-certificates-item>
      </topic-list>
    </base-container>
  </div>
</template>

<script>
import SidebarBlock from './components/sidebar/SidebarBlock.vue';
import TopicList from './components/topics/TopicList.vue';

import TopicEducationItem from './components/topics/TopicEducationItem.vue';
import TopicExperienceItem from './components/topics/TopicExperienceItem.vue';
import TopicSkillsItem from './components/topics/TopicSkillsItem.vue';
import TopicProjectsItem from './components/topics/TopicProjectsItem.vue';
import TopicCertificatesItem from './components/topics/TopicCertificatesItem.vue';
import TheHeader from './components/layout/TheHeader.vue';

import personalData from './personalData.js';

export default {
  name: 'App',
  components: {
    TheHeader,
    SidebarBlock,
    TopicList,
    TopicEducationItem,
    TopicExperienceItem,
    TopicSkillsItem,
    TopicProjectsItem,
    TopicCertificatesItem,
  },

  methods: {
    openSidebar() {
      this.isMobileSidebarOpen = true;
    },
    closeSidebar() {
      this.isMobileSidebarOpen = false;
    }
  },
  computed: {
    containerClasses() {
      return {
        ['is-sidebar-open']: this.isMobileSidebarOpen
      }
    }
  },
  data() {
    return {
      isMobileSidebarOpen: false,
      topics: personalData.topics,
      contacts: personalData.contacts
    }
  }
}
</script>

